<template>
  <el-row>
    <el-steps :active="active" finish-status="success">
      <el-step title=""></el-step>
      <el-step title="NAR11"></el-step>
      <el-step title="NAR12"></el-step>
      <el-step title="ND2A"></el-step>
      <el-step title="NR1" :status="forthStatus"></el-step>
    </el-steps>

    <!--  page1 -->
    <el-form v-show="active === 1" class="page-content" ref="formPage1" :model="form" :rules="rules" label-width="150px">
      <el-row class="module-title">
        A.
      </el-row>
      <el-row class="form-item-container">
        <el-form-item label="" prop="busiNumPage1">
          <el-input v-model="form.busiNumPage1" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="busiNamePage1">
          <el-input v-model="form.busiNamePage1" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="originAddrPage1">
          <el-input v-model="form.originAddrPage1" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="newAddrPage1">
          <el-input v-model="form.newAddrPage1" placeholder=""></el-input>
        </el-form-item>
      </el-row>
    </el-form>

    <!-- NAR1 page2 -->
    <el-form v-show="active ===2" class="page-content" ref="formPage2" :model="form" :rules="rules" label-width="150px">
      <el-row class="module-title">
        A. Annual Return
      </el-row>
      <el-row class="form-item-container">
        <el-form-item label=":" prop="cNum">
          <el-input v-model="form.cNum" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="cNameEn">
          <el-input v-model="form.cNameEn" placeholder=" LIMITED "></el-input>
        </el-form-item>
        <el-form-item label=":" prop="cNameCn">
          <el-input v-model="form.cNameCn" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="settleDate">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.settleDate"
            style="width: 50%;"
            format="dd/MM/yyyy"
            value-format="dd/MM/yyyy"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label=":" prop="cRAddr">
          <el-input v-model="form.cRAddr" placeholder=""></el-input>
        </el-form-item>
      </el-row>
      <el-row class="module-title">B.Presentors Reference</el-row>
      <el-row class="form-item-container">
        <el-form-item label="">
          <el-radio-group v-model="submitUser.userInfo" @change="changeSubmitUser">
            <el-radio label="defaultUser"></el-radio>
            <el-radio label="selfUser"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":" prop="sUName">
          <el-input v-model="form.sUName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="sUAddr">
          <el-input v-model="form.sUAddr" placeholder=""></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label=":" prop="sUPhone">
              <el-input v-model="form.sUPhone" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=":" prop="sUFax">
              <el-input v-model="form.sUFax" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label=":" prop="sUEmail">
          <el-input v-model="form.sUEmail" placeholder="" type="email"></el-input>
        </el-form-item>
      </el-row>
      <el-row class="module-title">C.Share Capital</el-row>
      <el-row class="form-item-container">
        <el-row>
          <el-col :span="12">
            <el-form-item label=":" prop="cSType">
              <el-input v-model="form.cSType" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=":" prop="cSCurrency">
              <el-input v-model="form.cSCurrency" placeholder="(HKD)"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-form-item label=":" prop="cSTotal">
            <el-input v-model="form.cSTotal" placeholder="()"></el-input>
          </el-form-item>
        </el-row>
      </el-row>
    </el-form>

    <!-- NAR1 page3 -->
    <el-form v-show="active ===3" class="page-content" ref="formPage3" :model="form" :rules="rules" label-width="150px">
      <el-row class="module-title">
        A. () Company Secretary
      </el-row>
      <el-row>
        <el-form-item label=":" prop="cNameCnPage3">
          <el-input v-model="form.cNameCnPage3" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="cNameEnPage3">
          <el-input v-model="form.cNameEnPage3" placeholder=" LIMITED "></el-input>
        </el-form-item>
        <el-form-item label=":" prop="hkAddrPage3">
          <el-input v-model="form.hkAddrPage3" placeholder=""></el-input>
        </el-form-item>
      </el-row>
      <el-row class="module-title">
        B. () Director (Natural Person)
      </el-row>
      <el-row>
        <el-form-item label=":" prop="dNameCnPage3">
          <el-input v-model="form.dNameCnPage3" placeholder=""></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label="-:" prop="dNameEn1Page3">
              <el-input v-model="form.dNameEn1Page3" placeholder="-"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="-:" prop="dNameEn2Page3">
              <el-input v-model="form.dNameEn2Page3" placeholder="-"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label=":" prop="dAddrPage3">
          <el-input v-model="form.dAddrPage3" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="dIdNumPage3">
          <el-input v-model="form.dIdNumPage3" type="number" placeholder=""></el-input>
        </el-form-item>
         <el-form-item label=":" prop="sharePage3">
          <el-input v-model="form.sharePage3" type="number" placeholder=""></el-input>
        </el-form-item>
      </el-row>
    </el-form>

    <!-- ND2A page4 -->
    <el-form v-show="active ===4" class="page-content" ref="formPage4" :model="form" :rules="rules" label-width="150px">
      <el-row class="module-title">
        A.
      </el-row>
    </el-form>

    <!-- NR1 page5 -->
    <el-form v-show="active ===5" class="page-content" ref="formPage5" :model="form" :rules="rules" label-width="150px">
      <el-row class="module-title">
        A.
      </el-row>
    </el-form>

    <el-row class="btn-row">
      <el-button @click="prePage" v-show="active !== 1"></el-button>
      <el-button @click="nextPage" v-show="active !== 4"></el-button>
      <el-button @click="nextPage" v-show="active == 4"></el-button>
    </el-row>
  </el-row>
</template>

<script>
import defaultInfo from '@/config/defaultInfo'
import { ProduceWordZip } from '@/api'

export default {
  data() {
    return {
      active: 4,
      forthStatus: 'wait',

      form: {
        busiNumPage1: '',
        busiNamePage1: '',
        originAddrPage1: '',
        newAddrPage1: '',
        cNum: '',
        cNameEn: '',
        cNameCn: '',
        settleDate: '',

        cRAddr: defaultInfo.workAddrCn,

        sUName: defaultInfo.userName,
        sUAddr: defaultInfo.userAddr,
        sUPhone: defaultInfo.userPhone,
        sUFax: defaultInfo.userFax,
        sUEmail: defaultInfo.userEmail,

        cSType: defaultInfo.companyStockTypeCn,
        cSCurrency: defaultInfo.companyCurrenyCn,
        cSTotal: defaultInfo.stockTotal,

        cNameCnPage3: '',
        cNameEnPage3: '',
        hkAddrPage3: '',
        dNameCnPage3: '',
        dNameEn1Page3: '',
        dNameEn2Page3: '',
        dAddrPage3: '',
        dIdNumPage3: '',
        sharePage3: ''
      },
      submitUser: {
        userInfo: 'defaultUser'
      },

      rules: {
        busiNumPage1: [{ required: true, message: '' }],
        busiNamePage1: [{ required: true, message: '' }],
        originAddrPage1: [{ required: true, message: '' }],
        newAddrPage1: [{ required: true, message: '' }],
        cNum: [{ required: true, message: '' }],
        cNameEn: [{ required: true, message: '' }],
        cNameCn: [{ required: true, message: '' }],
        settleDate: [{ required: true, message: '' }],

        sUName: [{ required: true, message: '' }],
        sUAddr: [{ required: true, message: '' }],
        sUPhone: [{ required: true, message: '' }],
        sUFax: [{ required: true, message: '' }],
        sUEmail: [{ required: true, message: '' }],

        cSType: [{ required: true, message: '' }],
        cSCurrency: [{ required: true, message: '' }],
        cSTotal: [{ required: true, message: '' }],

        cNameCnPage3: [{ required: true, message: '' }],
        cNameEnPage3: [{ required: true, message: '' }],
        hkAddrPage3: [{ required: true, message: '' }],
        dNameCnPage3: [{ required: true, message: '' }],
        dNameEn1Page3: [{ required: true, message: '-' }],
        dNameEn2Page3: [{ required: true, message: '-' }],
        dAddrPage3: [{ required: true, message: '' }],
        dIdNumPage3: [
          {
            required: true,
            message: ''
          }
        ],
        sharePage3: [
          {
            required: true,
            message: ''
          }
        ]
      }
    }
  },

  methods: {
    changeSubmitUser(value) {
      // 
      if (value == 'selfUser') {
        this.form = {
          ...this.form,
          sUName: '',
          sUAddr: '',
          sUPhone: '',
          sUFax: '',
          sUEmail: '',
          cSType: ''
        }
      } else {
        this.form = {
          ...this.form,
          sUName: defaultInfo.userName,
          sUAddr: defaultInfo.userAddr,
          sUPhone: defaultInfo.userPhone,
          sUFax: defaultInfo.userFax,
          sUEmail: defaultInfo.userEmail,
          cSType: defaultInfo.companyStockType
        }
      }
    },

    prePage() {
      if (this.active > 1) {
        this.active -= 1
      }
      this.forthStatus = 'wait'
    },

    nextPage() {
      const formObj = {
        1: 'formPage1',
        2: 'formPage2',
        3: 'formPage3',
        4: 'formPage4',
        5: 'formPage5'
      }

      this.$refs[formObj[this.active]].validate(valid => {
        if (valid) {
          if (this.active < 5) {
            this.active = this.active + 1
          } else {
            const params = Object.assign({}, this.form)
            const [
              settleDateD,
              settleDateM,
              settleDateY
            ] = params.settleDate.split('/')
            params.sDateY = settleDateY
            params.sDateM = settleDateM
            params.sDateD = settleDateD
            console.log(params)
            const wordName = ['NS1', 'NS2', 'NS3', 'NS4']
            // this.produceWord(wordName, 'NS-CN', params)
          }
        } else {
          return false
        }
      })
    },

    produceWord(wordName, tempName, wordParams) {
      const data = {
        docxName: wordName,
        tempName: tempName,
        docxParams: wordParams
      }

      ProduceWordZip(data)
        .then(() => {
          this.forthStatus = 'success'
          const url = '/output/NS-CN.zip'
          const fileName = 'NS-CN.zip'
          this.$alert(
            `<a href=${url} download=${fileName}>${fileName}</a>`,
            '',
            {
              dangerouslyUseHTMLString: true
            }
          )
        })
        .catch(({ code, msg }) => {
          this.$message({
            message: msg,
            type: 'error',
            duration: 2000
          })
        })
    }
  }
}
</script>

<style scoped>
</style>



