<script>
import defaultInfo from '@/config/defaultInfo'
import { ProduceWord } from '@/api'

export default {
  data() {
    const validateFunderNameEn = (rule, value, cb) => {
      const arr = value.split(' ')
      if (arr[0] && arr[1]) {
        cb()
      } else {
        cb(new Error(''))
      }
    }

    const validateCName = (rule, value, cb) => {
      const cnameReg = /(LIMITED|limited)$/
      if (cnameReg.test(value)) {
        cb()
      } else {
        cb(new Error(' LIMITED / limited '))
      }
    }

    return {
      active: 0,
      forthStatus: 'wait',
      form: {
        language: 'cn',
        cNameEn: '',
        cNameCn: '',
        cRAddr: defaultInfo.workAddrCn,
        sUName: defaultInfo.userName,
        sUAddr: defaultInfo.userAddr,
        sUPhone: defaultInfo.userPhone,
        sUFax: defaultInfo.userFax,
        sUEmail: defaultInfo.userEmail,
        cSType: defaultInfo.companyStockTypeCn,
        cSCurrency: defaultInfo.companyCurrenyCn,
        cSTotal: defaultInfo.stockTotal,
        fNameCn: '',
        fNameEn: '',
        fAddr: '',
        fCountry: '',
        fStockNum: '',

        fNameCn1: 'NIL',
        fNameEn1: 'NIL',
        fAddr1: 'NIL',
        fCountry1: 'NIL',
        fStockNum1: 'NIL',
        cSType1: 'NIL',
        cSCurrency1: 'NIL',
        fStockNumC1: 'NIL',

        // 
        isChairman: false,
        // 
        isOtherChairman: false,

        cNumber: defaultInfo.companyNumber,
        cEmail: 'NIL',
        sNameCn: defaultInfo.secretaryNameCn,
        sNameEn: defaultInfo.secretaryNameEn,
        sHKAddr: defaultInfo.secretaryHKAddr,

        chNameCn: '',
        chSureName: '',
        chOtherName: '',
        chCountry: '',
        chAddr: '',

        // fNameSigned: '',
        fDateSigned: '',

        // 
        certificateType: 'chinaId',
        chChinaId: '',
        chHKId: 'NIL',
        psCountry: 'NIL',
        psNumber: 'NIL',

        // id9
        idN1: 'NIL',
        idN2: 'NIL',
        idN3: 'NIL',
        idN4: 'NIL',
        idN5: 'NIL',
        idN6: 'NIL',
        idN7: 'NIL',
        idN8: 'NIL',
        idN9: 'NIL'
      },
      submitUser: {
        userInfo: 'defaultUser'
      },
      rules: {
        cNameEn: [
          { required: true, message: '' },
          { validator: validateCName }
        ],
        cNameCn: [
          { required: true, message: '' },
          { validator: validateCName }
        ],
        cRAddr: [{ required: true, message: '' }],
        sUName: [{ required: true, message: '' }],
        sUAddr: [{ required: true, message: '' }],
        sUPhone: [{ required: true, message: '' }],
        sUFax: [{ required: true, message: '' }],
        sUEmail: [{ required: true, message: '' }],

        cSType: [{ required: true, message: '' }],
        cSCurrency: [{ required: true, message: '' }],
        cSTotal: [{ required: true, message: '' }],
        fNameCn: [{ required: true, message: '' }],
        fNameEn: [
          { required: true, message: '' },
          { validator: validateFunderNameEn }
        ],
        fAddr: [{ required: true, message: '' }],
        fCountry: [{ required: true, message: '' }],
        fStockNum: [{ required: true, message: '' }],

        fNameCn1: [{ required: true, message: '' }],
        fNameEn1: [{ required: true, message: '' }],
        fAddr1: [{ required: true, message: '' }],
        fCountry1: [{ required: true, message: '' }],
        fStockNum1: [{ required: true, message: '' }],

        cNumber: [{ required: true, message: '' }],
        // companyEmail: [
        //   { required: true, message: '' }
        // ],
        sNameCn: [{ required: true, message: '' }],
        sNameEn: [{ required: true, message: '' }],
        sHKAddr: [{ required: true, message: '' }],

        chNameCn: [{ required: true, message: '' }],
        chSureName: [{ required: true, message: '' }],
        chOtherName: [{ required: true, message: '' }],
        chCountry: [{ required: true, message: '' }],
        chAddr: [{ required: true, message: '' }],
        chChinaId: [
          { required: true, message: '' }
          // { validator: validateRequire }
        ],
        chHKId: [
          { required: true, message: '' }
          // { validator: validateRequire }
        ],
        psCountry: [
          { required: true, message: '' }
          // { validator: validateRequire }
        ],
        psNumber: [
          { required: true, message: '' }
          // { validator: validateRequire }
        ],
        // fNameSigned: [{ required: true, message: '' }],
        fDateSigned: [{ required: true, message: '' }]
      }
    }
  },

  methods: {
    changeLanguage(value) {
      if (value == 'cn') {
        this.form = {
          ...this.form,
          cRAddr: defaultInfo.workAddrCn,
          cSType: defaultInfo.companyStockTypeCn,
          cSCurrency: defaultInfo.companyCurrenyCn,
          cSTotal: defaultInfo.stockTotal
        }
      } else {
        this.form = {
          ...this.form,
          cRAddr: defaultInfo.workAddrEn,
          cSType: defaultInfo.companyStockTypeEn,
          cSCurrency: defaultInfo.companyCurrenyEn,
          cSTotal: defaultInfo.stockTotal
        }
      }
    },

    changeOtherMem(value) {
      if (!value) {
        this.form = {
          ...this.form,
          fNameCn1: 'NIL',
          fNameEn1: 'NIL',
          fAddr1: 'NIL',
          fCountry1: 'NIL',
          fStockNum1: 'NIL'
        }
      }
    },

    changeSubmitUser(value) {
      // 
      if (value == 'selfUser') {
        this.form = {
          ...this.form,
          sUName: '',
          sUAddr: '',
          sUPhone: '',
          sUFax: '',
          sUEmail: '',
          cSType: ''
        }
      } else {
        this.form = {
          ...this.form,
          sUName: defaultInfo.userName,
          sUAddr: defaultInfo.userAddr,
          sUPhone: defaultInfo.userPhone,
          sUFax: defaultInfo.userFax,
          sUEmail: defaultInfo.userEmail,
          cSType: defaultInfo.companyStockType
        }
      }
    },

    changeCertificateType(value) {
      this.form = {
        ...this.form,
        chChinaId: value == 'chinaId' ? '' : 'NIL',
        chHKId: value == 'hkId' ? '' : 'NIL',
        psCountry: value == 'passport' ? '' : 'NIL',
        psNumber: value == 'passport' ? '' : 'NIL'
      }
    },

    prePage() {
      if (this.active > 0) {
        this.active = this.active - 1
      }
      this.forthStatus = 'wait'
    },

    nextPage() {
      const formObj = {
        0: 'formPage0',
        1: 'formPage1',
        2: 'formPage2',
        3: 'formPage3'
      }
      this.$refs[formObj[this.active]].validate(valid => {
        if (valid) {
          if (this.active < 3) {
            // 2
            if (this.active == 1 && this.form.isChairman) {
              const { fNameCn, fAddr, fCountry } = this.form
              const [chSureName, chOtherName] = this.form.fNameEn.split('-')
              this.form = {
                ...this.form,
                chNameCn: fNameCn,
                chSureName,
                chOtherName,
                chAddr: fAddr,
                chCountry: fCountry
              }
            }
            this.active = this.active + 1
          } else {
            const params = Object.assign({}, this.form)
            delete params.isChairman
            delete params.language
            params.fNameEn = params.fNameEn.split('-').join(' ')
            // console.log(params)
            //  HKID
            if (params.certificateType == 'hkId') {
              const [
                hkId1 = 'NIL',
                hkId2 = 'NIL',
                hkId3 = 'NIL',
                hkId4 = 'NIL',
                hkId5 = 'NIL',
                hkId6 = 'NIL',
                hkId7 = 'NIL',
                hkId8 = 'NIL',
                hkId9 = 'NIL'
              ] = Array.from(params.chHKId)
              console.log(Array.from(params.chHKId))
              params.idN1 = hkId1
              params.idN2 = hkId2
              params.idN3 = hkId3
              params.idN4 = hkId4
              params.idN5 = hkId5
              params.idN6 = hkId6
              params.idN7 = hkId7
              params.idN8 = hkId8
              params.idN9 = hkId9
            }
            delete params.certificateType
            // 
            if (params.isOtherChairman) {
              params.cSType1 = params.cSType
              params.cSCurrency1 = params.cSCurrency
              params.fStockNumC1 = params.cSCurrency + params.fStockNum1
            }
            delete params.isOtherChairman
            // console.log(params)
            this.produceWord('NNC1', params)
          }
        } else {
          return false
        }
      })
    },

    produceWord(wordName, wordParams) {
      const data = {
        docxName: wordName,
        docxParams: wordParams
      }

      ProduceWord(data)
        .then(() => {
          this.forthStatus = 'success'
          const url = `/output/${wordName}`
          const fileName = `${wordName}.docx`
          this.$alert(
            `<a href=${url} download=${fileName}>${fileName}</a>`,
            'WORD ',
            {
              dangerouslyUseHTMLString: true
            }
          )
        })
        .catch(({ code, msg }) => {
          this.$message({
            message: msg,
            type: 'error',
            duration: 2000
          })
        })
    }
  }
}
</script>

<style scoped>
.ncc1-page {
  margin-top: 20px;
}

.form-item-container {
  width: 80%;
  margin: 0 auto;
}

.nnc1-module-title {
  padding: 10px 0;
  border-bottom: 1px solid #333;
  margin-bottom: 16px;
}
</style>





