import { makeRequest } from '@/utils/restApi'

// 
export function Login(data) {
  return makeRequest({
    url: '/api/login',
    method: 'post',
    data
  })
}

// word
export function ProduceWord(data) {
  return makeRequest({
    url: '/api/produceword',
    method: 'post',
    data
  })
}

//  zip
export function ProduceWordZip(data) {
  return makeRequest({
    url: '/api/produceword-muti',
    method: 'post',
    data
  })
}



