<template>
  <el-row>
    <el-row style="padding: 20px 0;">
      <el-radio-group
        v-model="radio"
        @change="handleRadioChange"
      >
        <el-radio label="1"></el-radio>
        <el-radio label="2"></el-radio>
      </el-radio-group>
    </el-row>
    <el-steps
      :active="active"
      finish-status="success"
    >
      <el-step title=""></el-step>
      <el-step
        title=""
        :status="forthStatus"
      ></el-step>
    </el-steps>

    <!-- page1 -->
    <el-form
      v-show="active == 0"
      class="ncc1-page"
      ref="formPage0"
      :model="form"
      :rules="rules"
      label-width="150px"
    >
      <el-row class="nnc1-module-title">A. Transferor Information </el-row>
      <el-row class="form-item-container">
        <el-form-item
          label=":"
          prop="cNameEn"
        >
          <el-input
            v-model="form.cNameEn"
            placeholder=" LIMITED "
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="cNameCn"
        >
          <el-input
            v-model="form.cNameCn"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="tNameEn"
            >
              <el-input
                v-model="form.tNameEn"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="tNameCn"
            >
              <el-input
                v-model="form.tNameCn"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="tStockNum"
            >
              <el-input
                v-model="form.tStockNum"
                placeholder="HK Dollar(HK$)"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          label=":"
          prop="tAddr"
        >
          <el-input
            v-model="form.tAddr"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-row>
      <el-row class="nnc1-module-title">B. Transferee Information </el-row>
      <el-row class="form-item-container">
        <el-row>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="tNameEn1"
            >
              <el-input
                v-model="form.tNameEn1"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="tNameCn1"
            >
              <el-input
                v-model="form.tNameCn1"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          label=":"
          prop="tAddr1"
        >
          <el-input
            v-model="form.tAddr1"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-row>
      <el-row class="nnc1-module-title">C. Stock Information </el-row>
      <el-row class="form-item-container">
        <el-row>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="stockT"
            >
              <el-input
                v-model="form.stockT"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="stockPer"
            >
              <el-input
                v-model="form.stockPer"
                placeholder=",(HK$)"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-row>
    </el-form>

    <!-- page2 -->
    <el-form
      v-show="active == 1"
      class="ncc1-page"
      ref="formPage1"
      :model="form"
      :rules="rules"
      label-width="150px"
    >
      <el-row class="nnc1-module-title">A. Notice of Resignation of Company Secretary and Director </el-row>
      <el-row class="form-item-container">
        <el-form-item
          label=":"
          prop="busiNum"
        >
          <el-input
            v-model="form.busiNum"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="cNum"
        >
          <el-input
            v-model="form.cNum"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="czNameCn"
          v-show="radio == '2'"
        >
          <el-input
            v-model="form.czNameCn"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-row v-show="radio == '2'">
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="czNameEn1"
            >
              <el-input
                v-model="form.czNameEn1"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="czNameEn2"
            >
              <el-input
                v-model="form.czNameEn2"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          label="ChinaId:"
          prop="chinaId"
          v-show="radio == '2'"
        >
          <el-input
            v-model="form.chinaId"
            placeholder="ChinaId"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="czDate"
          v-show="radio == '2'"
        >
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.czDate"
            style="width: 100%;"
            format="dd/MM/yyyy"
            value-format="dd/MM/yyyy"
          ></el-date-picker>
        </el-form-item>
      </el-row>
      <el-row class="nnc1-module-title">B.Presentors Reference</el-row>
      <el-row class="form-item-container">
        <el-form-item label="">
          <el-radio-group
            v-model="submitUser.userInfo"
            @change="changeSubmitUser"
          >
            <el-radio label="defaultUser"></el-radio>
            <el-radio label="selfUser"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          label=":"
          prop="sUName"
        >
          <el-input
            v-model="form.sUName"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="sUAddr"
        >
          <el-input
            v-model="form.sUAddr"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="sUPhone"
            >
              <el-input
                v-model="form.sUPhone"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="sUFax"
            >
              <el-input
                v-model="form.sUFax"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          label=":"
          prop="sUEmail"
        >
          <el-input
            v-model="form.sUEmail"
            placeholder=""
            type="email"
          ></el-input>
        </el-form-item>
      </el-row>
    </el-form>

    <el-row class="btn-row">
      <el-button
        @click="prePage"
        v-show="active !== 0"
      ></el-button>
      <el-button
        @click="nextPage"
        v-show="active !== 1"
      ></el-button>
      <el-button
        @click="nextPage"
        v-show="active == 1"
      ></el-button>
    </el-row>
  </el-row>
</template>

<script>
import defaultInfo from '@/config/defaultInfo'
import { ProduceWord } from '@/api'

const validateCName = (rule, value, cb) => {
  const cnameReg = /(LIMITED|limited)$/
  if (cnameReg.test(value)) {
    cb()
  } else {
    cb(new Error(' LIMITED / limited '))
  }
}

const defaultRules = {
  cNameEn: [
    { required: true, message: '' },
    { validator: validateCName }
  ],
  cNameCn: [
    { required: true, message: '' },
    { validator: validateCName }
  ],
  tNameEn: [{ required: true, message: '' }],
  tNameCn: [{ required: true, message: '' }],
  tStockNum: [{ required: true, message: '' }],
  tAddr: [{ required: true, message: '' }],

  tNameEn1: [{ required: true, message: '' }],
  tNameCn1: [{ required: true, message: '' }],
  tAddr1: [{ required: true, message: '' }],

  stockT: [{ required: true, message: '' }],
  stockPer: [{ required: true, message: '' }],

  busiNum: [{ required: true, message: '' }],
  cNum: [{ required: true, message: '' }],

  sUName: [{ required: true, message: '' }],
  sUAddr: [{ required: true, message: '' }],
  sUPhone: [{ required: true, message: '' }],
  sUFax: [{ required: true, message: '' }],
  sUEmail: [{ required: true, message: '' }]
}

export default {
  data() {
    return {
      radio: '1',

      active: 0,
      forthStatus: 'wait',

      form: {
        cNameEn: '',
        cNameCn: '',
        // 
        tNameEn: '',
        tNameCn: '',
        tStockNum: '',
        tAddr: '',

        // 
        tNameEn1: '',
        tNameCn1: '',
        tAddr1: '',

        stockT: '',
        stockPer: '',

        busiNum: '',
        cNum: '',
        czNameCn: '',
        czNameEn1: '',
        czNameEn2: '',

        chinaId: '',
        czDate: '',

        sUName: defaultInfo.userName,
        sUAddr: defaultInfo.userAddr,
        sUPhone: defaultInfo.userPhone,
        sUFax: defaultInfo.userFax,
        sUEmail: defaultInfo.userEmail
      },
      rules: Object.assign({}, defaultRules),
      submitUser: {
        userInfo: 'defaultUser'
      }
    }
  },

  methods: {
    prePage() {
      if (this.active > 0) {
        this.active = this.active - 1
      }
      this.forthStatus = 'wait'
    },

    nextPage() {
      const formObj = {
        0: 'formPage0',
        1: 'formPage1',
        2: 'formPage2',
        3: 'formPage3'
      }

      this.$refs[formObj[this.active]].validate(valid => {
        if (valid) {
          if (this.active < 1) {
            this.active = this.active + 1
          } else {
            const params = Object.assign({}, this.form)

            if (params.czDate) {
              const [dateD, dateM, dateY] = params.czDate.split('/')
              params.dateY = dateY
              params.dateM = dateM
              params.dateD = dateD
            }
            // console.log(params)
            this.produceWord('GGGD', params)
          }
        } else {
          return false
        }
      })
    },

    changeSubmitUser(value) {
      // 
      if (value == 'selfUser') {
        this.form = {
          ...this.form,
          sUName: '',
          sUAddr: '',
          sUPhone: '',
          sUFax: '',
          sUEmail: ''
        }
      } else {
        this.form = {
          ...this.form,
          sUName: defaultInfo.userName,
          sUAddr: defaultInfo.userAddr,
          sUPhone: defaultInfo.userPhone,
          sUFax: defaultInfo.userFax,
          sUEmail: defaultInfo.userEmail
        }
      }
    },

    produceWord(wordName, wordParams) {
      const data = {
        docxName: wordName,
        docxParams: wordParams
      }

      ProduceWord(data)
        .then(() => {
          this.forthStatus = 'success'
          const url = `/output/${wordName}`
          const fileName = `${wordName}.docx`
          this.$alert(
            `<a href=${url} download=${fileName}>.docx</a>`,
            'WORD ',
            {
              dangerouslyUseHTMLString: true
            }
          )
        })
        .catch(({ code, msg }) => {
          this.$message({
            message: msg,
            type: 'error',
            duration: 2000
          })
        })
    },

    handleRadioChange(radio) {
      console.log(this.rules, 11111)
      // if (radio === '1') {
      //   this.rules = Object.assign({}, defaultRules)
      // } else if (radio === '2') {
      //   const rulesCzObj = {
      //     czNameCn: [{ required: true, message: '' }],
      //     czNameEn1: [{ required: true, message: '' }],
      //     czNameEn2: [{ required: true, message: '' }],
      //     chinaId: [{ required: true, message: 'chinaId' }],
      //     czDate: [{ required: true, message: '' }]
      //   }
      //   this.rules = Object.assign(this.rules, rulesCzObj)
      // }
      // console.log(this.rules, 12)
    }
  }
}
</script>


<style scoped>
.ncc1-page {
  margin-top: 20px;
}

.form-item-container {
  width: 80%;
  margin: 0 auto;
}

.nnc1-module-title {
  padding: 10px 0;
  border-bottom: 1px solid #333;
  margin-bottom: 16px;
}

.btn-row {
  width: 80%;
  margin: 0 auto;
  text-align: right;
}
</style>



