<template>
  <el-row>
    <el-steps
      :active="active"
      finish-status="success"
    >
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step
        title=""
        :status="forthStatus"
      ></el-step>
    </el-steps>

    <!-- page1 -->
    <el-form
      v-show="active == 0"
      class="ncc1-page"
      ref="formPage0"
      :model="form"
      :rules="rules"
      label-width="150px"
    >
      <el-row class="nnc1-module-title">A. Cessation to Act as Company SecretaryDirector</el-row>
      <el-row class="form-item-container">
        <el-form-item
          label=":"
          prop="cNumber"
        >
          <el-input
            v-model="form.cNumber"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="cNameEn"
        >
          <el-input
            v-model="form.cNameEn"
            placeholder=" LIMITED "
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="cNameCn"
        >
          <el-input
            v-model="form.cNameCn"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-row>

      <el-row class="nnc1-module-title">B. () Particulars of Company SecretaryDirector</el-row>
      <el-row class="form-item-container">
        <el-row>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="chNameCn"
            >
              <el-input
                v-model="form.chNameCn"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="chSureName"
            >
              <el-input
                v-model="form.chSureName"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="chOtherName"
            >
              <el-input
                v-model="form.chOtherName"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label=":">
          <el-radio-group
            v-model="form.certificateType"
            @change="changeCertificateType"
          >
            <el-radio label="chinaId">CHINA ID</el-radio>
            <el-radio label="hkId">HongKong ID</el-radio>
            <el-radio label="passport"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          v-show="form.certificateType == 'chinaId'"
          label="ID:"
          prop="chChinaId"
        >
          <el-input
            v-model="form.chChinaId"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          v-show="form.certificateType == 'hkId'"
          prop="chHKId"
        >
          <el-input
            v-model="form.chHKId"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-row v-show="form.certificateType == 'passport'">
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="psCountry"
            >
              <el-input
                v-model="form.psCountry"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="psNumber"
            >
              <el-input
                v-model="form.psNumber"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          label=":"
          prop="leaveDate"
        >
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.leaveDate"
            style="width: 100%;"
            format="dd/MM/yyyy"
            value-format="dd/MM/yyyy"
          ></el-date-picker>
        </el-form-item>
      </el-row>
    </el-form>

    <!-- page2 -->
    <el-form
      v-show="active == 1"
      class="ncc1-page"
      ref="formPage1"
      :model="form"
      :rules="rules"
      label-width="150px"
    >
      <el-row class="nnc1-module-title">Presentors Reference</el-row>
      <el-row class="form-item-container">
        <el-form-item label="">
          <el-radio-group
            v-model="submitUser.userInfo"
            @change="changeSubmitUser"
          >
            <el-radio label="defaultUser"></el-radio>
            <el-radio label="selfUser"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          label=":"
          prop="sUName"
        >
          <el-input
            v-model="form.sUName"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="sUAddr"
        >
          <el-input
            v-model="form.sUAddr"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="sUPhone"
            >
              <el-input
                v-model="form.sUPhone"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="sUFax"
            >
              <el-input
                v-model="form.sUFax"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          label=":"
          prop="sUEmail"
        >
          <el-input
            v-model="form.sUEmail"
            placeholder=""
            type="email"
          ></el-input>
        </el-form-item>
      </el-row>
    </el-form>

    <!-- page3 -->
    <el-form
      v-show="active == 2"
      class="ncc1-page"
      ref="formPage2"
      :model="form"
      :rules="rules"
      label-width="150px"
    >
      <el-row class="nnc1-module-title">Appointment of Company SecretaryDirector</el-row>
      <el-row class="form-item-container">
        <el-row>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="wNameCn"
            >
              <el-input
                v-model="form.wNameCn"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="wSureName"
            >
              <el-input
                v-model="form.wSureName"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
              label=":"
              prop="wOtherName"
            >
              <el-input
                v-model="form.wOtherName"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item
          label=":"
          prop="wAddr"
        >
          <el-input
            v-model="form.wAddr"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          prop="wCountry"
        >
          <el-input
            v-model="form.wCountry"
            placeholder=""
          ></el-input>
        </el-form-item>

        <el-form-item label=":">
          <el-radio-group
            v-model="form.certificateType2"
            @change="changeCertificateType2"
          >
            <el-radio label="wchinaId"></el-radio>
            <el-radio label="whkId"></el-radio>
            <el-radio label="wpassport"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          v-show="form.certificateType2 == 'wchinaId'"
          label="ID:"
          prop="wChinaId"
        >
          <el-input
            v-model="form.wChinaId"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          v-show="form.certificateType2 == 'whkId'"
          prop="wHKId"
        >
          <el-input
            v-model="form.wHKId"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-row v-show="form.certificateType2 == 'wpassport'">
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="wpsCountry"
            >
              <el-input
                v-model="form.wpsCountry"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item
              label=":"
              prop="wpsNumber"
            >
              <el-input
                v-model="form.wpsNumber"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item
          label=":"
          prop="wDate"
        >
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.wDate"
            style="width: 100%;"
            format="dd/MM/yyyy"
            value-format="dd/MM/yyyy"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=":"
          prop="signDate"
        >
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.signDate"
            style="width: 100%;"
            format="dd/MM/yyyy"
            value-format="dd/MM/yyyy"
          ></el-date-picker>
        </el-form-item>
      </el-row>
    </el-form>

    <el-row class="btn-row">
      <el-button
        @click="prePage"
        v-show="active !== 0"
      ></el-button>
      <el-button
        @click="nextPage"
        v-show="active !== 2"
      ></el-button>
      <el-button
        @click="nextPage"
        v-show="active == 2"
      ></el-button>
    </el-row>
  </el-row>
</template>

<script>
import defaultInfo from '@/config/defaultInfo'
import { ProduceWord } from '@/api'

export default {
  data() {
    return {
      active: 0,
      forthStatus: 'wait',

      form: {
        cNumber: '',
        cNameEn: '',
        cNameCn: '',

        chNameCn: '',
        chSureName: '',
        chOtherName: '',

        // 
        certificateType: 'chinaId',
        chChinaId: '',
        chHKId: 'NIL',
        psCountry: 'NIL',
        psNumber: 'NIL',

        // id9
        idN1: 'NIL',
        idN2: 'NIL',
        idN3: 'NIL',
        idN4: 'NIL',
        idN5: 'NIL',
        idN6: 'NIL',
        idN7: 'NIL',
        idN8: 'NIL',
        idN9: 'NIL',
        leaveDate: '',

        sUName: defaultInfo.userName,
        sUAddr: defaultInfo.userAddr,
        sUPhone: defaultInfo.userPhone,
        sUFax: defaultInfo.userFax,
        sUEmail: defaultInfo.userEmail,

        wNameCn: '',
        wSureName: '',
        wOtherName: '',
        wAddr: '',
        wCountry: '',
        wDate: '',

        // 
        certificateType2: 'wchinaId',
        wChinaId: '',
        wHKId: 'NIL',
        wpsCountry: 'NIL',
        wpsNumber: 'NIL',

        // id9
        widN1: 'NIL',
        widN2: 'NIL',
        widN3: 'NIL',
        widN4: 'NIL',
        widN5: 'NIL',
        widN6: 'NIL',
        widN7: 'NIL',
        widN8: 'NIL',
        widN9: 'NIL',

        signDate: ''
      },
      rules: {
        cNumber: [{ required: true, message: '' }],
        cNameEn: [{ required: true, message: '' }],
        cNameCn: [{ required: true, message: '' }],
        chNameCn: [{ required: true, message: '' }],
        chSureName: [{ required: true, message: '' }],
        chOtherName: [{ required: true, message: '' }],
        chChinaId: [{ required: true, message: '' }],
        chHKId: [{ required: true, message: '' }],
        psCountry: [{ required: true, message: '' }],
        psNumber: [{ required: true, message: '' }],

        leaveDate: [{ required: true, message: '' }],

        sUName: [{ required: true, message: '' }],
        sUAddr: [{ required: true, message: '' }],
        sUPhone: [{ required: true, message: '' }],
        sUFax: [{ required: true, message: '' }],
        sUEmail: [{ required: true, message: '' }],

        wNameCn: [{ required: true, message: '' }],
        wSureName: [{ required: true, message: '' }],
        wOtherName: [{ required: true, message: '' }],
        wAddr: [{ required: true, message: '' }],
        wCountry: [{ required: true, message: '' }],
        wChinaId: [{ required: true, message: '' }],
        wHKId: [{ required: true, message: '' }],
        wpsCountry: [{ required: true, message: '' }],
        wpsNumber: [{ required: true, message: '' }],
        wDate: [{ required: true, message: '' }],
        signDate: [{ required: true, message: '' }]
      },
      submitUser: {
        userInfo: 'defaultUser'
      }
    }
  },

  methods: {
    changeSubmitUser(value) {
      // 
      if (value == 'selfUser') {
        this.form = {
          ...this.form,
          sUName: '',
          sUAddr: '',
          sUPhone: '',
          sUFax: '',
          sUEmail: ''
        }
      } else {
        this.form = {
          ...this.form,
          sUName: defaultInfo.userName,
          sUAddr: defaultInfo.userAddr,
          sUPhone: defaultInfo.userPhone,
          sUFax: defaultInfo.userFax,
          sUEmail: defaultInfo.userEmail
        }
      }
    },

    changeCertificateType(value) {
      this.form = {
        ...this.form,
        chChinaId: value == 'chinaId' ? '' : 'NIL',
        chHKId: value == 'hkId' ? '' : 'NIL',
        psCountry: value == 'passport' ? '' : 'NIL',
        psNumber: value == 'passport' ? '' : 'NIL'
      }
    },

    changeCertificateType2(value) {
      this.form = {
        ...this.form,
        wChinaId: value == 'wchinaId' ? '' : 'NIL',
        wHKId: value == 'whkId' ? '' : 'NIL',
        wpsCountry: value == 'wpassport' ? '' : 'NIL',
        wpsNumber: value == 'wpassport' ? '' : 'NIL'
      }
    },

    prePage() {
      if (this.active > 0) {
        this.active = this.active - 1
      }
      this.forthStatus = 'wait'
    },

    nextPage() {
      const formObj = {
        0: 'formPage0',
        1: 'formPage1',
        2: 'formPage2'
      }
      this.$refs[formObj[this.active]].validate(valid => {
        if (valid) {
          if (this.active < 2) {
            this.active = this.active + 1
          } else {
            const params = Object.assign({}, this.form)
            console.log(params)
            params.leaveY = params.leaveDate.split('/')[2]
            params.leaveM = params.leaveDate.split('/')[1]
            params.leaveD = params.leaveDate.split('/')[0]
            params.wYY = params.wDate.split('/')[2]
            params.wMM = params.wDate.split('/')[1]
            params.wDD = params.wDate.split('/')[0]
            //  HKID
            if (params.certificateType == 'hkId') {
              const [
                hkId1 = 'NIL',
                hkId2 = 'NIL',
                hkId3 = 'NIL',
                hkId4 = 'NIL',
                hkId5 = 'NIL',
                hkId6 = 'NIL',
                hkId7 = 'NIL',
                hkId8 = 'NIL',
                hkId9 = 'NIL'
              ] = Array.from(params.chHKId)
              console.log(Array.from(params.chHKId))
              params.idN1 = hkId1
              params.idN2 = hkId2
              params.idN3 = hkId3
              params.idN4 = hkId4
              params.idN5 = hkId5
              params.idN6 = hkId6
              params.idN7 = hkId7
              params.idN8 = hkId8
              params.idN9 = hkId9
            }
            delete params.certificateType

            //  HKID
            if (params.certificateType2 == 'whkId') {
              const [
                hkId1 = 'NIL',
                hkId2 = 'NIL',
                hkId3 = 'NIL',
                hkId4 = 'NIL',
                hkId5 = 'NIL',
                hkId6 = 'NIL',
                hkId7 = 'NIL',
                hkId8 = 'NIL',
                hkId9 = 'NIL'
              ] = Array.from(params.wHKId)
              console.log(Array.from(params.chHKId))
              params.widN1 = hkId1
              params.widN2 = hkId2
              params.widN3 = hkId3
              params.widN4 = hkId4
              params.widN5 = hkId5
              params.widN6 = hkId6
              params.widN7 = hkId7
              params.widN8 = hkId8
              params.widN9 = hkId9
            }
            delete params.certificateType2
            // console.log(params)
            this.produceWord('ND2A', params)
          }
        } else {
          return false
        }
      })
    },

    produceWord(wordName, wordParams) {
      const data = {
        docxName: wordName,
        docxParams: wordParams
      }

      ProduceWord(data)
        .then(() => {
          this.forthStatus = 'success'
          const url = `/output/${wordName}`
          const fileName = `${wordName}.docx`
          this.$alert(
            `<a href=${url} download=${fileName}>${fileName}</a>`,
            'WORD ',
            {
              dangerouslyUseHTMLString: true
            }
          )
        })
        .catch(({ code, msg }) => {
          this.$message({
            message: msg,
            type: 'error',
            duration: 2000
          })
        })
    }
  }
}
</script>

<style scoped>
.ncc1-page {
  margin-top: 20px;
}

.form-item-container {
  width: 80%;
  margin: 0 auto;
}

.nnc1-module-title {
  padding: 10px 0;
  border-bottom: 1px solid #333;
  margin-bottom: 16px;
}

.btn-row {
  width: 80%;
  margin: 0 auto;
  text-align: right;
}
</style>




